// ** React Imports
import moment from 'moment'
import { Link } from 'react-router-dom'
import { Badge } from 'reactstrap'
import { indianFormattedPrice } from '@src/helper/functionHelper.js'
const dateFormat = (value) => {
  if (value === undefined || value === null) {
    return ''
  }
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }

}

const paymentstatusObj = [

  { code: '5', name: "Pending", color: 'light-danger' },
  { code: '6', name: "Partially Paid", color: 'light-warning' },
  { code: '7', name: 'Paid', color: 'light-success' },
  { code: '11', name: 'Sent', color: 'light-secondary' }
]

const renderStatus = (val) => {
  const obj = paymentstatusObj.find((o) => parseInt(o.code) === parseInt(val))
  return obj
}

export const columns = [
  {
    name: 'Invoice No',
    sortable: true,
    minWidth: '170px',
    sortField: 'uniqueidentity',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        <div className='d-flex flex-column'>
          <Link
            to={`/invoice/view/${row.invoiceid}`}
            className='user_name text-truncate text-body'>
            <span className='fw-bolder'>{row.invoiceno}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Invoice Amount',
    sortable: true,
    minWidth: '108px',
    selector: row => row.totalamount,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totalamount)}</span>
  },
  {
    name: 'Payment Status',
    sortable: true,
    minWidth: '108px',
    selector: row => row.paymentstatus,
    cell: row => {
      const sts = renderStatus(row.paymentstatus) || {}
      return (
        <Badge color={sts?.color || 'light-primary'}>{sts.name || row.paymentstatus}</Badge>
      )
    }
  },
  {
    name: 'Payment No',
    sortable: true,
    minWidth: '108px',
    selector: row => row.uniqueno,
    cell: row => <span className='text-capitalize'>{row.uniqueno}</span>
  },
  {
    name: 'Payment Date',
    sortable: true,
    minWidth: '108px',
    selector: row => row.paymentdate,
    cell: row => <span className='text-capitalize'>{dateFormat(row.paymentdate)}</span>
  },
  {
    name: 'Reference No',
    sortable: true,
    minWidth: '108px',
    selector: row => row.referenceno,
    cell: row => <span className='text-capitalize'>{row.referenceno || 0}</span>
  },
  {
    name: 'Amount Paid',
    sortable: true,
    minWidth: '108px',
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.totalamountpaid || 0)}</span>
  },
  {
    name: 'With Held',
    sortable: true,
    minWidth: '108px',
    selector: row => row.amountwithheld,
    cell: row => <span className='text-capitalize'>{indianFormattedPrice(row.amountwithheld || 0)}</span>
  }
]
